@charset "utf-8";

@import "../common/base";
@import "../common/variables";
@import "../common/mixins";

body {
    font-family: $defaultFont;
    color: $textColor;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    &.chat-open {
        &:before {
            background-color: rgba(0, 0, 0, 0.5);
            position: fixed;
            content: "";
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            z-index: 9;
        }
    }
}

/* hide up/down arrows ("spinners") on input fields marked type=number */
.no-spinners[type='number'] {
    -moz-appearance:textfield;
}

.no-spinners::-webkit-outer-spin-button,
.no-spinners::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

.account-container {
    width: $accountContainer;
}

.forgot-password {
    color: $accent;
    text-decoration: none;
}

.terms, .blue {
    color: $blueColor !important;
}

.dgt-link-remove {
    line-height: $baseLineHeight + 8;
}

.dgt-padding {
    padding: 24px;
}

.dgt-padding-small {
    padding: 0 24px;
}

.form-group {
    margin-bottom: 20px;
    border-bottom: 1px solid #EDEDED;
    padding: 0 0 16px 0;
}

.dgt-text-overlay {
    margin-bottom: 30px;
}

.dgt-heading {
    margin-bottom: 50px;
}

.dgt-wrapper {
    padding: 0;
}

.dgt-icon {
    width: 16px;
    height: 16px;
    background: url($spriteUrl) no-repeat 0 0;
    margin-right: 12px;
    display: block;
}

.icon-lock {
    background-position: 0 -18px;
}

.icon-plane {
    background-position: 0 -34px;
}

.dgt-control {
    input, textarea, .control-value {
        outline: none;
        z-index: 1;
        position: relative;
        background: none;
        width: 100%;
        height: 36px;
        border: 0;
        color: $inputColor;
        font-size: $baseFontSize + 2;
        font-weight: 400;
        border: none;
        box-shadow: none;
        display: block;
        &:focus {
            ~ .bar:before, ~ .bar:after {
                width: 50%;
            }
        }
    }
    textarea {
        padding: 10px 0;
        resize: none;
        height: 46px;
    }
    label {
        font-size: $baseFontSize;
    }
    .bar {
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 1px;
        &:before, &:after {
            content: '';
            position: absolute;
            background: $blueColor;
            width: 0;
            height: 2px;
            -webkit-transition: .2s ease;
            transition: .2s ease;
        }
        &:before {
            left: 50%;
        }
        &:after {
            right: 50%;
        }
    }
    ::-webkit-input-placeholder { /* Chrome/Opera/Safari */
        color: $inputPlaceholder;
    }
    ::-moz-placeholder { /* Firefox 19+ */
        color: $inputPlaceholder;
    }
    :-ms-input-placeholder { /* IE 10+ */
        color: $inputPlaceholder;
    }
    :-moz-placeholder { /* Firefox 18- */
        color: $inputPlaceholder;
    }
}

.dgt-control-selecter {
    margin-bottom: 30px;
    .selecter-item {
        min-width: 140px;
        color: #7B7B7B;
        line-height: 30px;
        text-align: center;
        padding: 0 10px;
        display: inline-block;
        @include border-radius(16px, 16px, 16px, 16px);
        margin: 0 10px 0 0;
        &:hover, &.active {
            border-color: $accent;
            text-decoration: none;
        }
        &.active {
            color: $accent;
        }
    }
}

.dgt-control-checkbox {
    .checkbox-item {
        margin-bottom: 13px;
        &:last-child {
            margin-bottom: 0;
        }
    }
    label {
        color: #4A4A4A;
    }
    /* Base for label styling */
    [type="checkbox"]:not(:checked),
    [type="checkbox"]:checked {
        position: absolute;
        left: -9999px;
    }
    [type="checkbox"]:not(:checked) + label,
    [type="checkbox"]:checked + label {
        position: relative;
        padding-left: 20px;
        cursor: pointer;
    }

    /* checkbox aspect */
    [type="checkbox"]:not(:checked) + label:before,
    [type="checkbox"]:checked + label:before {
        content: '';
        position: absolute;
        left: 0;
        top: 5px;
        width: 12px;
        height: 12px;
        border: 1px solid #979797;
        background: #fff;
        @include border-radius(2px, 2px, 2px, 2px);
    }
    /* checked mark aspect */
    [type="checkbox"]:not(:checked) + label:after,
    [type="checkbox"]:checked + label:after {
        content: '';
        position: absolute;
        top: 7px;
        left: 2px;
        width: 8px;
        height: 8px;
        background-color: $accent;
    }
    /* checked mark aspect changes */
    [type="checkbox"]:not(:checked) + label:after {
        opacity: 0;
        transform: scale(0);
    }
    [type="checkbox"]:checked + label:after {
        opacity: 1;
        transform: scale(1);
    }
}

.dgt-control-radio {
    .control-radio-item {
        margin-bottom: 13px;
        &:last-child {
            margin-bottom: 0;
        }
    }
    .dgt-control-date {
        margin-top: 10px;
    }
    [type="radio"]:checked,
    [type="radio"]:not(:checked) {
        position: absolute;
        left: -9999px;
    }
    [type="radio"]:checked + label,
    [type="radio"]:not(:checked) + label {
        position: relative;
        padding-left: 20px;
        cursor: pointer;
        line-height: 20px;
        display: block;
        text-align: left;
        color: #030303;
    }
    [type="radio"]:checked + label:before,
    [type="radio"]:not(:checked) + label:before {
        content: '';
        position: absolute;
        left: 0;
        top: 4px;
        width: 12px;
        height: 12px;
        border: 1px solid #979797;
        border-radius: 100%;
        background: #fff;
    }
    [type="radio"]:checked + label:after,
    [type="radio"]:not(:checked) + label:after {
        content: '';
        width: 6px;
        height: 6px;
        background: $accent;
        position: absolute;
        top: 7px;
        left: 3px;
        border-radius: 100%;
    }
    [type="radio"]:not(:checked) + label:after {
        opacity: 0;
        -webkit-transform: scale(0);
        transform: scale(0);
    }
    [type="radio"]:checked + label:after {
        opacity: 1;
        -webkit-transform: scale(1);
        transform: scale(1);
    }
}

.radio-addon {
    font-size: $baseFontSize;
    color: $blueColor;
    margin-left: 10px;
    cursor: pointer;
    &:hover {
        color: $blueColor;
    }
    .dgt-icon-custom {
        margin-right: 5px;
    }
}

/* Fly Control Upload */
.dgt-control-upload {
    input[type="file"] {
        position: absolute;
        width: 0;
        height: 0;
        visibility: hidden;
        @include opacity(0);
    }
    .input-addon {
        display: block;
        position: relative;
        cursor: pointer;
        color: $link;
        margin: 10px 0 0;
        &:before {
            width: 120px;
            height: 32px;
            content: "Choose file";
            border: 1px solid $linkHover;
            color: $linkHover;
            @include border-radius(16px, 16px, 16px, 16px);
            display: inline-block;
            text-align: center;
            line-height: $baseLineHeight + 6;
            margin-right: 16px;
        }
    }
}

.dgt-control-select {
    label {
        display: block;
        padding-bottom: 0;
    }
}

.control-select-alt {
    label {
        float: left;
        margin-right: 10px;
    }
    .select2-container--default {
        width: auto !important;
    }
}

.dgt-control-note {
    color: #787A7C;
    font-size: $baseFontSize - 2;
    display: block;
    padding: 10px 0 0;
}

.dgt-field-note {
    position: absolute;
    right: 0;
    bottom: 13px;
    font-size: $baseFontSize;
}

.dgt-datepicker {
    label {
        display: table-cell;
        vertical-align: middle;
        padding-left: 10px;
    }
}

/* General */
.top-link {
    margin-bottom: 32px;
    ul {
        display: flex;
        li {
            &:not(:last-child) {
                margin: 0 40px 0 0;
            }
            a {
                @include opacity(0.2);
                font-size: 24px;
                &:hover, &.active {
                    @include opacity(1);
                    color: $link;
                }
            }
        }
    }
}

.top-heading {
    h1 {
        font-size: $baseFontSize + 10;
        text-transform: uppercase;
    }
}

#soffos-header, #soffos-footer {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
}

#soffos-footer {
    top: auto;
    bottom: 0;
    z-index: 9;
}

/* Header */
#soffos-header {
    overflow: hidden;
    background-color: $accent;
    height: 80px;
    z-index: 3;
    &:before {
        width: 50%;
        height: 100%;
        content: "";
        position: absolute;
        background-color: #fff;
        left: -25%;
        top: 0;
        @include css3-compatible("transform", rotate(0deg) scale(1) skew(-40deg) translate(0));
        display: block;
    }
    #logo {
        padding-left: 25px;
        img {
            max-height: 25px;
        }
    }
    #logo-second {
        img {
            max-height: 50px;
        }
    }
}

/* Crew Nav */
#soffos-nav {
    border-top: 1px solid #EDEDED;
    padding: 0 15px;
    position: fixed;
    left: 0;
    width: 100%;
    top: 80px;
    z-index: 2;
    background-color: $whiteColor;
    @include css3-compatible("box-shadow", 0 1px 10px 0 rgba(0, 0, 0, 0.1));
    .nav-link {
        padding: 18px 0;
        margin: 0 20px;
        color: $navLink;
        border-bottom: 2px solid transparent;
        &.active {
            color: $link;
            border-bottom-color: $accent;
        }
    }
    + #soffos-main-inner{
        padding-top: 93px;
    }
}

/* Crew Table */
.dgt-table{
    border-radius: 6px 6px 0 0;
    position: relative;
    overflow: hidden;
    background-color: $whiteColor;
    @include css3-compatible("box-shadow", 0 2px 15px 0 rgba(0,0,0,0.1));
    .thead{
        background-color: $tHead;
        color: $whiteColor;
        font-weight: bold;
        line-height: $baseLineHeight + 32;
        padding: 0 16px;
    }
    .name{
        padding-left: 18px;
    }
    .product{
        padding-right: 50px;
    }
    .delete{
        width: 32px;
        font-size: 18px;
        color: $grayColor;
    }
    .tbody{
        color: $blackColor;
        background-color: $tBody;
        .total{
            color: $accent;
            font-weight: bold;
        }
        .icon-order{
            width: 32px;
            height: 32px;
            background-color: $accent;
            @include border-radius(50%, 50%, 50%, 50%);
            display: flex;
            justify-content: center;
            align-items: center;
            color: $whiteColor;
            font-size: $baseFontSize + 4;
            cursor: pointer;
        }
        .item{
            padding: 12px 16px;
            word-break: break-all;
        }
        .parent{
            border-bottom: 1px solid $whiteColor;
            position: relative;
            .action{
                height: 32px;
            }
        }
        .item-wrapper{
            &:last-child{
                .parent{
                    border-bottom: none;
                }
            }
        }
        .sub-item{
            padding: 0 16px;
            background-color: $whiteColor;
            font-size: $baseFontSize;
            color: rgba(0,0,0,0.5);
            .total{
                color: rgba(0,0,0,0.5);
                font-weight: normal;
            }
            .item{
                padding: 13px 0;
                border-bottom: 1px solid #F5F5F5;
            }
        }
    }
}

.sub-item{
    display: none;
}
.open-sub{
    .sub-item{
        display: block;
    }
}

/* Cover */
#soffos-cover {
    min-height: 100vh;
    max-width: 400px;
    position: relative;
    width: 400px;
    z-index: -1;
    .dgt-cover-inner {
        position: absolute;
        width: 100%;
        height: 100%;
    }
    img {
        height: 100%;
        max-width: 100%;
        max-height: 100%;
        object-fit: cover;
        object-position: center center;
    }
}

/* Form */
.dgt-account-form {
    .top-heading {
        margin: 84px 0 90px;
    }
}

.dgt-form-button {
    margin: 32px 25px 0;
}

/* Content */
#soffos-content {
    min-height: 100vh;
}

#soffos-main-content {
    padding: 80px 0 72px;
}

#soffos-main-inner{
    padding: 30px;
}

/* Footer */
#soffos-footer {
    height: 72px;
    padding: 0 32px;
    width: 100%;
    color: $footerColor;
    background-color: $whiteColor;
    font-size: $baseFontSize + 2;
    border-top: 1px solid #EDEDED;
}

.toggle-chat {
    width: 40px;
    height: 40px;
    @include border-radius(50%, 50%, 50%, 50%);
    background-color: $accent;
    display: flex;
    justify-content: center;
    margin-right: 13px;
    line-height: $baseLineHeight + 20;
    cursor: pointer;
    i {
        color: $whiteColor;
        font-size: $baseFontSize + 10;
    }
}

.toggle-chat-close {
    width: 64px;
    height: 64px;
    background-color: $whiteColor;
    line-height: $baseLineHeight + 32;
    position: absolute;
    left: calc(50% - 32px);
    top: -32px;
    margin-right: 0;
    i {
        color: $accent;
        font-size: $baseFontSize + 6;
    }
}

.chat-notification {
    .mins {
        color: $accent;
        margin-left: 16px;
    }
}

#soffos-form-chat {
    input {
        border: 2px solid #D8D8D8;
        border-radius: 20px;
        margin-right: 13px;
        font-size: 14px;
        padding: 0 20px;
    }
}

#form-chat-message {
    background-color: $whiteColor;
    position: fixed;
    left: 0;
    bottom: 72px;
    width: 100%;
    height: 0;
    @include opacity(0);
    visibility: hidden;
    .response {
        padding: 24px;
        padding-bottom: 5px;
    }
}

.chat-open {
    #form-chat-message {
        height: calc(100% - 72px - 185px);
        @include opacity(1);
        visibility: visible;
    }
}

/* Crew Checkout */
#soffos-checkout{
    margin-top: 90px;
    form{
        flex: 0 0 51%;
        > div{
            border: 2px solid #D8D8D8;
            border-radius: 28px;
            @include border-radius(28px, 28px, 28px, 28px);
            padding: 0 16px;
        }
        input{
            border: none;
            font-size: $baseFontSize + 6;
            color: $grayColor;
        }
        .input-prepend{
            margin-right: 10px;
        }
        i{
            font-size: $baseFontSize + 10;
            color: $grayColor;
        }
    }
    #checkout-button{
        flex: 0 0 29%;
        text-transform: uppercase;
        font-weight: bold;
        i{
            font-size: $baseFontSize + 6;
        }
    }
}
.quantity{
    display: flex;
    border: 1px solid #D8D8D8;
    position: relative;
    overflow: hidden;
    width: 84px;
    margin-left: -32px;
    @include border-radius(4px, 4px, 4px, 4px);
    button{
        background-color: $whiteColor;
        border: none;
        cursor: pointer;
        width: 28px;
        height: 28px;
        outline: none;
        padding: 0;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .qty{
        width: 28px;
        height: 28px;
        background-color: $whiteColor;
        border: 1px solid #D8D8D8;
        border-width: 0 1px;
        text-align: center;
        color: $blackColor;
        font-size: $baseFontSize;
    }
}

@import "responsive";

